/*       (768px  ) */
@media screen and (max-width: 768px) {
  /*       */
  .alphabet-buttons {
    display: none;
  }
  /*   */
  .alphabet-select {
    display: flex;
  }
  /*   ::after   .choices,    data-type    "select-one"    */
  .choices::after {
    display: none;
  }

  /*     .choices */
  .choices {
    width: 90px;
    margin-left: auto;
    margin-right: auto;

    text-align: left;
  }

  /*     .choices__inner */
  .choices__inner {
    width: 90px;
    height: 34px;
    background-color: rgba(126, 143, 221, 0.4);
    color: #fdfdff;
    border-radius: 8px;
    border: none;
    padding: 8px 14px;
    appearance: none;
    background-image: url('/src/images/chevron-down.svg');
    background-repeat: no-repeat;
    background-position: 80%; /*  c */

    /* margin-top: 40px; */
    /* !     */
    margin-bottom: 40px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  /*   .choices__inner,  .choices     */
  .is-open .choices__inner {
    background-color: var(--additional-accent-color);
    color: var(--primary-color-interface);
    border-radius: 8px;
    border: none;
    padding: 8px 15px;
    appearance: none;
    background-image: url('/src/images/chevron-up.svg');
    background-repeat: no-repeat;
    background-position: 80%; /*  c */
    justify-content: space-between;
    align-items: center;

    /* !     */
    margin-bottom: 4px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .choices__list--dropdown,
  .choices__list[aria-expanded] {
    overflow-y: scroll;
    max-height: 84px;
  }

  /*        */
  .choices__list--dropdown::-webkit-scrollbar {
    width: 3px;
    background-color: transparent;

    /* !   !!!!            4px */
    /*        4px */
    /* right: 4px;  */
    /* margin-right: 4px; */
    /* margin-right: auto; */
  }

  /*    ()      */
  .choices__list--dropdown::-webkit-scrollbar-thumb {
    height: 34px;
    background-color: var(--additional-accent-color); /*   */
    border-radius: 4px; /*    */
  }

  /*     .choices__list--dropdown  .choices__list[aria-expanded] */
  .is-open .choices__list--dropdown,
  .is-open .choices__list[aria-expanded] {
    font-size: 16px;
    background-color: #fdfdff;
    color: #242424;
    border: 1px solid var(--additional-accent-color);
    border-radius: 4px;
    font-family: 'Poppins', serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    width: 90px;
    height: 84px;
  }

  /*     .choices__item    */
  .choices__list--dropdown .choices__item,
  .choices__list[aria-expanded] .choices__item {
    padding: 4px 10px;
  }

  /*     .choices__item,  .choices__list--single */
  .choices__list--single .choices__item {
    font-family: 'Poppins', serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
  }

  .choices__list--dropdown .choices__item--selectable.is-highlighted,
  .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
    background-color: transparent;
  }

  /*       choices__inner             choices__inner */
  .choices.is-focused .choices__inner {
    background-color: var(--additional-accent-color);
    color: #fdfdff;
    background-repeat: no-repeat;
    background-position: 80%; /*  c */

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  /*     */
  .choices__list--dropdown .choices__list,
  .choices__list[aria-expanded] .choices__list {
    position: relative;
    max-height: 650px;
    overflow: hidden;
  }
}

/* ====================================================== */
/*       ( 768px) */
@media screen and (min-width: 769px) {
  /*                 */
  .alphabet-buttons {
    display: flex;
    row-gap: 16px;
    column-gap: 8px;
    flex-wrap: wrap;
    justify-content: center;

    margin-top: 100px;
    margin-bottom: 50px;
  }

  /*       */
  .alphabet-select,
  .choices {
    display: none;
  }
}

/*        */
@media screen and (min-width: 769px) {
  .letter-button {
    width: 40px;
    height: 40px;

    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    background-color: rgba(126, 143, 221, 0.4);
    color: var(--primary-color-interface);

    border-radius: 8px;
    border: none;

    padding: 0;

    /*        */
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    color: #fdfdff;
  }

  /*            */
  .letter-button:hover,
  .letter-button:focus {
    background-color: var(--additional-accent-color);
  }
}
