:root {
  --main-font-family: "Poppins", sans-serif;
  --accent-color: #7e8fdd;
  --additional-accent-color: #9cdfdf;
  --primary-white-color: #fdfdff;
  --primary-transparent-white-color: rgba(253, 253, 255, .5);
  --primary-black-color: #242424;
  --secondary-color-interface: #242424;
  --primary-transparent-black-color: rgba(36, 36, 36, .5);
  --primary-button-backgroun: #7e8fdd;
  --primary-button-accent-backgroun: #6e7fcf;
  --secondary-button-backgroun: rgba(36, 36, 36, .1);
  --secondary-button-accent-backgroun: rgba(36, 36, 36, .2);
  --secondary-button-backgroun-dark: rgba(253, 253, 255, .1);
  --secondary-button-accent-backgroun-dark: rgba(253, 253, 255, .2);
  --opacity-50: .5;
  --opacity-20: .2;
  --opacity-10: .1;
  --hover-btn-add-favorite: #6e7fcf;
}

[data-theme="light"] {
  --primary-color-text: var(--primary-black-color);
  --primary-transparent-color-text: var(--primary-transparent-black-color);
  --primary-color-interface: var(--primary-white-color);
  --secondary-color-interface: #242424;
  --back-button-background: var(--secondary-button-backgroun);
  --back-button-accent-background: var(--secondary-button-accent-backgroun);
  --bg-color: #f8f9ff;
  --bg-image-mob: url("back-light-mob.825ff634.webp");
  --bg-image-tablet: url("back-light-tablet.f6160d78.webp");
  --bg-image-desktop: url("back-light-desktop.c6aa64f0.webp");
  --bg-color-checked: transparent;
  --border-checked: var(--primary-black-color);
  --span-checked: var(--additional-accent-color);
  --bg-modal: #fdfdff;
  --title-modal: var(--primary-black-color);
  --li-ingredients: rgba(36, 36, 36, .8);
  --bg-back-color: rgba(36, 36, 36, .1);
  --bg-back-color-hover: rgba(36, 36, 36, .2);
}

[data-theme="dark"] {
  --primary-color-text: var(--primary-white-color);
  --primary-transparent-color-text: var(--primary-transparent-white-color);
  --primary-color-interface: var(--primary-black-color);
  --secondary-color-interface: #f8f9ff;
  --back-button-background: var(--secondary-button-backgroun-dark);
  --back-button-accent-background: var(--secondary-button-accent-backgroun-dark);
  --bg-color: #000;
  --bg-color-checked: var(--additional-accent-color);
  --border-checked: var(--accent-color);
  --span-checked: var(--primary-white-color);
  --bg-modal: #232330;
  --title-modal: var(--primary-white-color);
  --li-ingredients: rgba(253, 253, 255, .992);
  --bg-back-color: rgba(253, 253, 255, .1);
  --bg-back-color-hover: rgba(253, 253, 255, .2);
}

.ingredients {
  color: var(--primary-color-text);
  padding: 80px 20px;
  font-family: Poppins;
}

.ingredients-cards {
  flex-wrap: wrap;
  margin-top: 40px;
  display: flex;
}

.ingredients-main-title {
  margin: 0;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.ingredient-card {
  max-height: 222px;
  max-width: 335px;
  background-color: var(--primary-color-interface);
  border-radius: 20px;
  flex-direction: column;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

.ingredient-title {
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
}

.alcohol-level, .ingredient-description {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.alcohol-level {
  margin: 0 0 14px;
  padding: 0;
}

.ingredient-description {
  text-overflow: ellipsis;
  white-space: normal;
  margin: 0 0 20px;
  padding: 0;
  overflow: hidden;
}

.alcohol-level {
  opacity: var(--opacity-50);
}

.ingredients-buttons {
  align-items: center;
  gap: 15px;
  margin-top: auto;
  display: flex;
}

.learn-more-button {
  width: 100%;
  min-height: 48px;
  background-color: var(--primary-white-color);
  border-width: 1px;
  border-color: var(--accent-color);
  text-transform: uppercase;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
}

.learn-more-button:hover, .learn-more-button:focus {
  color: var(--primary-color-interface);
  background-color: var(--accent-color);
}

.ingredients-button-remove {
  min-width: 48px;
  min-height: 48px;
  background-color: var(--accent-color);
  border-width: 0;
  border-radius: 12px;
}

.icon-trash {
  pointer-events: none;
}

.ingredients-placeholder {
  max-width: 244px;
  text-align: center;
  flex-direction: column;
  align-items: center;
  margin: 138px auto;
  font-size: 18px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.33;
  display: flex;
}

.placeholder-text-colored {
  color: var(--additional-accent-color);
}

@media screen and (min-width: 768px) {
  .ingredients {
    padding: 100px 32px;
  }

  .ingredients-cards {
    margin-top: 32px;
  }

  .ingredient-card {
    width: calc(50% - 8px);
    max-height: 242px;
    max-width: 344px;
    margin-bottom: 32px;
    margin-right: 16px;
  }

  .ingredients-main-title {
    font-size: 40px;
    line-height: 1.2;
  }

  .ingredient-title {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
  }

  .learn-more-button {
    font-size: 18px;
    line-height: 1.33;
  }

  .ingredients-placeholder {
    max-width: 359px;
    margin: 100px auto 140px;
    font-size: 24px;
    line-height: 1.17;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .ingredient-card:nth-child(2n) {
    margin-right: 0;
  }
}

@media screen and (min-width: 1280px) {
  .ingredients {
    padding: 100px 0 0;
  }

  .ingredients-cards {
    margin-top: 44px;
  }

  .ingredient-card {
    width: calc(33.3333% - 16px);
    max-height: 242px;
    max-width: 344px;
    margin-bottom: 44px;
    margin-right: 24px;
  }

  .ingredient-card:nth-child(3n) {
    margin-right: 0;
  }

  .ingredients-main-title {
    margin: 0;
    font-size: 40px;
    line-height: 1.2;
  }

  .ingredient-title {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
  }

  .learn-more-button {
    font-size: 18px;
    line-height: 1.33;
  }
}

