}

.header-open-modal-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  padding: 0;
}

.header-burger-icon {
  stroke: var(--primary-color-text);
  width: 32px;
  height: 32px;
  display: block;
}

@media screen and (min-width: 768px) {
  .header-switch-theme {
    display: none;
  }

  .section-header {
    padding-top: 25px;
    padding-bottom: 32px;
  }

  .header-logo-icon {
    stroke: var(--accent-color);
    width: 29px;
    height: 25px;
  }

  .header-link-text {
    color: var(--primary-color-text);
    letter-spacing: -.22px;
    font-size: 22px;
    line-height: 1.09;
  }

  .header-burger-icon {
    width: 40px;
    height: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .header-open-modal-btn, .burger-backdrop {
    display: none;
  }

  .header-nav {
    display: flex;
  }

  .section-header {
    padding-top: 28px;
    padding-bottom: 34px;
  }

  .header-list {
    column-gap: 18px;
    display: flex;
  }

  .header-list-link {
    color: var(--primary-color-text);
    letter-spacing: .32px;
    font-weight: 400;
    line-height: 1.12;
    display: inline-block;
    position: relative;
  }

  .header-list-link-icon {
    width: 12px;
    height: 20px;
    margin-left: 4px;
  }

  .header-list-item {
    position: relative;
  }

  .header-dropdown-link {
    color: rgba(253, 253, 255, .5);
    letter-spacing: .28px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.28;
  }

  .header-dropdown-link:hover, .header-dropdown-link:focus, .header-dropdown-link:active {
    color: var(--primary-white-color);
  }

  .header-dropdown {
    width: 192px;
    background-color: var(--accent-color);
    z-index: 8;
    border-radius: 16px;
    margin-top: 13px;
    padding: 18px 24px;
    transition: transform .5s cubic-bezier(.4, 0, .2, 1);
    display: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%)translateY(0);
  }

  .header-switch-theme {
    width: 50px;
    height: 26px;
    display: block;
    position: relative;
  }

  .header-switch-theme .switch-theme-input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .switch-theme-input {
    pointer-events: none;
  }

  .header-switch-theme-slider {
    cursor: pointer;
    background-color: var(--primary-white-color);
    border: 1px solid #242424;
    border-radius: 25px;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .header-switch-theme-slider:before {
    content: "";
    height: 22px;
    width: 22px;
    background-color: var(--additional-accent-color);
    border-radius: 50%;
    transition: all .4s;
    position: absolute;
    bottom: 1px;
    left: 1px;
  }

  .switch-theme-input:checked + .header-switch-theme-slider {
    background-color: #fff;
  }

  .switch-theme-input:focus + .header-switch-theme-slider {
    box-shadow: 0 0 1px var(--accent-color);
  }

  .switch-theme-input:checked + .header-switch-theme-slider:before {
    transform: translateX(25px);
  }

  .switch-theme-input:checked + .header-switch-theme-slider {
    background-color: var(--additional-accent-color);
  }

  .switch-theme-input:checked + .header-switch-theme-slider:before {
    background-color: #fff;
  }

  .header-list {
    align-items: center;
    display: flex;
  }

  .header-list-link {
    color: var(--primary-color-text);
    align-items: center;
    display: flex;
    position: relative;
  }

  .burger-dropdown {
    width: 192px;
    background-color: var(--accent-color);
    text-align: center;
    border-radius: 16px;
    padding: 18px 24px;
    display: none;
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    transform: translateX(-50%);
  }

  .burger-dropdown:before {
    content: "";
    width: 0;
    height: 0;
    border-left: 10px solid rgba(0, 0, 0, 0);
    border-right: 10px solid rgba(0, 0, 0, 0);
    border-bottom: 10px solid var(--accent-color);
    position: absolute;
    top: -10px;
    left: calc(50% - 10px);
  }

  .header-list-link:hover .header-dropdown {
    display: block;
  }
}

